import React from 'react';
import { Box, Container, Grid, Typography, IconButton, List, ListItem, Link } from '@mui/material';
import { Facebook, Twitter, Instagram, LinkedIn, YouTube, Phone, Email, LocationOn, Copyright } from '@mui/icons-material';
import { useTheme } from '@mui/material/styles';

function Footer() {
  const theme = useTheme();
  
  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: theme => theme.palette.mode === 'dark' ? '#1a1a1a' : '#f5f5f5',
        color: theme => theme.palette.mode === 'dark' ? '#ffffff' : '#333333',
        py: 4,
        mt: 'auto',
        borderTop: '1px solid',
        borderColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)',
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          {/* Quick Links Section */}
          <Grid size={{ xs: 12, sm: 6, md: 6 }}>
            <Typography 
              variant="h6" 
              fontWeight="bold" 
              gutterBottom
              sx={{ color: theme => theme.palette.mode === 'dark' ? '#ffffff' : '#333333' }}
            >
              Quick Links
            </Typography>
            <List dense>
              {[
                { text: 'Home', href: '#home' },
                { text: 'About Us', href: '#about' },
                { text: 'Contact', href: '#contact' }
              ].map((item) => (
                <ListItem key={item.text} disableGutters>
                  <Link 
                    href={item.href} 
                    color={theme => theme.palette.mode === 'dark' ? 'grey.300' : 'grey.700'}
                    underline="hover"
                    sx={{ 
                      '&:hover': { 
                        color: 'secondary.main',
                        transform: 'translateX(5px)',
                        transition: 'all 0.3s ease'
                      }
                    }}
                  >
                    {item.text}
                  </Link>
                </ListItem>
              ))}
            </List>
          </Grid>

          {/* Social Media Section */}
          <Grid size={{ xs: 12, sm: 6, md: 6 }}>
            <Typography 
              variant="h6" 
              fontWeight="bold" 
              gutterBottom
              sx={{ color: theme => theme.palette.mode === 'dark' ? '#ffffff' : '#333333' }}
            >
              Follow Us
            </Typography>
            <Box display="flex" gap={3} flexWrap="wrap">
              <IconButton
                aria-label="Facebook"
                href="https://facebook.com"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
                  color: theme => theme.palette.mode === 'dark' ? 'white' : 'black',
                  border: theme => theme.palette.mode === 'dark' ? '1px solid rgba(255, 255, 255, 0.2)' : '1px solid rgba(0, 0, 0, 0.1)',
                  '&:hover': {
                    backgroundColor: '#1877F2',
                    transform: 'translateY(-3px)',
                    transition: 'all 0.3s ease',
                    color: 'white',
                  },
                }}
              >
                <Facebook />
              </IconButton>
              <IconButton
                aria-label="Twitter"
                href="https://twitter.com"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
                  color: theme => theme.palette.mode === 'dark' ? 'white' : 'black',
                  border: theme => theme.palette.mode === 'dark' ? '1px solid rgba(255, 255, 255, 0.2)' : '1px solid rgba(0, 0, 0, 0.1)',
                  '&:hover': {
                    backgroundColor: '#1DA1F2',
                    transform: 'translateY(-3px)',
                    transition: 'all 0.3s ease',
                    color: 'white',
                  },
                }}
              >
                <Twitter />
              </IconButton>
              <IconButton
                aria-label="Instagram"
                href="https://instagram.com"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
                  color: theme => theme.palette.mode === 'dark' ? 'white' : 'black',
                  border: theme => theme.palette.mode === 'dark' ? '1px solid rgba(255, 255, 255, 0.2)' : '1px solid rgba(0, 0, 0, 0.1)',
                  '&:hover': {
                    backgroundColor: '#E4405F',
                    transform: 'translateY(-3px)',
                    transition: 'all 0.3s ease',
                    color: 'white',
                  },
                }}
              >
                <Instagram />
              </IconButton>
              <IconButton
                aria-label="LinkedIn"
                href="https://linkedin.com"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
                  color: theme => theme.palette.mode === 'dark' ? 'white' : 'black',
                  border: theme => theme.palette.mode === 'dark' ? '1px solid rgba(255, 255, 255, 0.2)' : '1px solid rgba(0, 0, 0, 0.1)',
                  '&:hover': {
                    backgroundColor: '#0A66C2',
                    transform: 'translateY(-3px)',
                    transition: 'all 0.3s ease',
                    color: 'white',
                  },
                }}
              >
                <LinkedIn />
              </IconButton>
              <IconButton
                aria-label="YouTube"
                href="https://youtube.com"
                target="_blank"
                rel="noopener noreferrer"
                sx={{
                  backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)',
                  color: theme => theme.palette.mode === 'dark' ? 'white' : 'black',
                  border: theme => theme.palette.mode === 'dark' ? '1px solid rgba(255, 255, 255, 0.2)' : '1px solid rgba(0, 0, 0, 0.1)',
                  '&:hover': {
                    backgroundColor: '#FF0000',
                    transform: 'translateY(-3px)',
                    transition: 'all 0.3s ease',
                    color: 'white',
                  },
                }}
              >
                <YouTube />
              </IconButton>
            </Box>
          </Grid>
        </Grid>

        {/* Copyright Section */}
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          sx={{ 
            mt: 4, 
            pt: 3, 
            borderTop: '1px solid',
            borderColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)',
            color: theme => theme.palette.mode === 'dark' ? 'grey.400' : 'grey.600'
          }}
        >
          <Box display="flex" alignItems="center">
            <Copyright sx={{ fontSize: 14, mr: 0.5 }} />
            <Typography 
              variant="body2"
              sx={{ color: 'inherit' }}
            >
              2025 Your Company. All rights reserved.
            </Typography>
          </Box>
        </Box>
      </Container>
    </Box>
  );
}

export default Footer;
